import React, { Suspense, lazy } from "react";
import AcademicDepartmentManagerProfile from "./AcademicDepartmentManagerProfile";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useInstituteUserDepartmentDashboard } from "../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";

const InstituteAcademicDepartmentBatch = lazy(() =>
  import(
    "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentBatch/InstituteAcademicDepartmentBatch"
  )
);
const InstituteAcademicDepartmentMaster = lazy(() =>
  import(
    "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentMaster/InstituteAcademicDepartmentMaster"
  )
);
const InstituteAcademicDepartmentCls = lazy(() =>
  import(
    "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentCls/InstituteAcademicDepartmentCls"
  )
);
const UserMemberContainerWrapper = lazy(() =>
  import("../../Ui/UserMember/UserMemberContainerWrapper")
);
const AcademicDepartmentManagerClass = lazy(() =>
  import("./AcademicDepartmentManagerClass")
);
const InstituteTheoryClassDetail = lazy(() =>
  import(
    "../../InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstituteTheoryClassDetail/InstituteTheoryClassDetail"
  )
);
const InstitutePracticalBatchClassDetail = lazy(() =>
  import(
    "../../InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstitutePracticalBatchClassDetail/InstitutePracticalBatchClassDetail"
  )
);
const StudentStatitics = lazy(() =>
  import("../FinanceManager/StudentStatitics/StudentStatitics")
);
const StudentStatiticsDetail = lazy(() =>
  import("../FinanceManager/StudentStatitics/StudentStatiticsDetail")
);
const DepartmentOngoingApplication = lazy(() =>
  import("./DepartmentOngoingApplication/DepartmentOngoingApplication")
);
const OneOngoingDepartmentConfig = lazy(() =>
  import("./DepartmentOneOngoingApplication/OneOngoingDepartmentConfig")
);
const ApplicationSubjectListDetail = lazy(() =>
  import(
    "../AdmissionManager/OneOngoingAdmission/ApplicationSubjectList/ApplicationSubjectListDetail"
  )
);

const AcademicDepartmentManager = () => {
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const { instituteUserDepartmentDashboard } =
    useInstituteUserDepartmentDashboard({
      data: {
        did: getQuery.state?.onClickValue?._id,
      },
      skip: !getQuery.state?.onClickValue?._id,
    });

  // console.info("dgbjshdgs", instituteUserDepartmentDashboard);
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ||
          ["staff", "student"]?.includes(searchParams.get("type")) ? (
            <AcademicDepartmentManagerProfile
              did={getQuery.state?.onClickValue?._id}
            />
          ) : // searchParams.get("type") === "staff" ? (
          //   <UserMemberContainerWrapper>
          //     <InstituteAcademicDepartmentStaff
          //       did={getQuery.state?.onClickValue?._id}
          //       openAs="DEPARTMENT_HEAD"
          //     />
          //   </UserMemberContainerWrapper>
          // ) : searchParams.get("type") === "student" ? (
          //   <UserMemberContainerWrapper>
          //     <InstituteAcademicDepartmentStudent
          //       did={getQuery.state?.onClickValue?._id}
          //       openAs="DEPARTMENT_HEAD"
          //     />
          //   </UserMemberContainerWrapper>
          // ) :
          searchParams.get("type") === "batch" ? (
            <UserMemberContainerWrapper>
              <InstituteAcademicDepartmentBatch
                did={getQuery.state?.onClickValue?._id}
                openAs="DEPARTMENT_HEAD"
                selectBatchId={
                  instituteUserDepartmentDashboard?.active_academic_batch?._id
                }
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "master" ? (
            <UserMemberContainerWrapper>
              <InstituteAcademicDepartmentMaster
                did={getQuery.state?.onClickValue?._id}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                openAs="DEPARTMENT_HEAD"
                departmentName={instituteUserDepartmentDashboard?.dName ?? ""}
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "cls" ? (
            <UserMemberContainerWrapper>
              <InstituteAcademicDepartmentCls
                did={getQuery.state?.onClickValue?._id}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                openAs="DEPARTMENT_HEAD"
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "cls-detail" ? (
            <UserMemberContainerWrapper>
              <AcademicDepartmentManagerClass
                did={getQuery.state?.onClickValue?._id}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "theory-detail" ? (
            <UserMemberContainerWrapper>
              <InstituteTheoryClassDetail
                clsId={getQuery.state?.clsId}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                openAs="DEPARTMENT_HEAD"
                // did={getQuery.state?.onClickValue?._id}
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "practical-detail" ? (
            <UserMemberContainerWrapper>
              <InstitutePracticalBatchClassDetail
                clsId={getQuery.state?.clsId}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                openAs="DEPARTMENT_HEAD"
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "statistics" ? (
            <UserMemberContainerWrapper>
              <StudentStatitics
                departmentData={instituteUserDepartmentDashboard}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                batchId={
                  instituteUserDepartmentDashboard?.active_academic_batch?._id
                }
                isFlow="DEPARTMENT"
                parentUrl={`/q/${params.username}/member/staff/academic/department/${params.name}?type=statistics`}
                isStyle={{
                  maxWidth: "910px",
                }}
                isUniversal="YES"
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "statistics-detail" ? (
            <StudentStatiticsDetail />
          ) : searchParams.get("type") === "ongoing" ? (
            <DepartmentOngoingApplication
              departmentId={getQuery.state?.onClickValue?._id}
              admissionId={
                instituteUserDepartmentDashboard?.institute
                  ?.admissionDepart?.[0]
              }
              // admissionId="651ba377e39dbdf817dd5291"
            />
          ) : searchParams.get("type") === "ongoing-detail" ? (
            <OneOngoingDepartmentConfig
              departmentId={getQuery.state?.onClickValue?._id}
              admissionId={
                instituteUserDepartmentDashboard?.institute
                  ?.admissionDepart?.[0]
              }
              financeId={
                instituteUserDepartmentDashboard?.institute?.financeDepart?.[0]
              }
              // admissionId="651ba377e39dbdf817dd5291"
              // financeId=""
            />
          ) : searchParams.get("type") === "selected-subject" ? (
            <ApplicationSubjectListDetail />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default AcademicDepartmentManager;
